// app/api/postcontact/route.js
import { NextResponse } from "next/server";


export async function POST(req) {
    try {
        const body = await req.json();
        const { username, email, description } = body;
        if (!username && !email && !description) {
            return NextResponse.json({ message: "Please fill all the fields", data:{username, email, description} }
            )
        }
        return NextResponse.json({message:"Message has been submitted successfuly"})
    }
    catch(error) {
        return NextResponse.json({error:"Invalid Response"},{status:"400"})
    }
}
